export default {
	data(){
		return {
			pages: {
				perPage: 8,
				currPage: 1,
				pageCount: 1,
        totalCount:1
			},
			items: [],
			loading: false,
			finished: true,
			isEmpty: false,
		}
	},

	methods: {
		async resetInit(params){
			this.resetData();

			let page = await this.initData();
			this.$nextTick(() => {
        this.setPages(page);
			})
		},
		isFinished() {
			this.finished = true;
			this.loading = false;
		},
		async loadMore() {
			const vm = this;
			if (vm.pages.pageCount < vm.pages.currPage) {
				vm.$toast({
					message: "没有更多了~",
					position: "top"
				});
				vm.isFinished();
			}else{
				let page = await vm.initData(true);
        // this.$nextTick(() => {
        //   this.setPages(page);
        // })
				vm.nextPage(page.pageCount);
			}
			vm.loading = false;
		},
		nextPage(pageCount = 1){
			this.pages.currPage += 1;
			this.pages.pageCount = pageCount;
		},
		setPages(page = {}){
			this.isEmpty = (page.totalCount === 0);
			if(page.totalCount <= this.pages.perPage){
				//不满一页
				this.isFinished();
			}else{
				//下一页
        this.finished = false;
				this.nextPage(page.pageCount);
			}
		},
		resetData(){
			this.items = [];
			this.pages = {
				perPage: 8,
				currPage: 1,
				pageCount: 1,
        totalCount:1
			}
			this.loading = false;
			this.finished = true;
			this.isEmpty = false;
		}
	}
}



