<template>
<div id="app">
  <van-list
    v-model="loading"
    :finished="finished"
    :immediate-check="false"
    :offset="100"
    @load="loadMore">
    <van-cell v-for="item in items" :key="item.id" :title="item.title" @click="openNotice(item)" is-link/>
  </van-list>

  <van-popup class="bounced" v-model="show" v-html="content"></van-popup>
</div>
</template>

<script>
  import {List, Cell} from 'vant'
  import { Popup } from 'vant'
  import { NOTICES } from "@/api/user";
  import loadMore from "@/vue/mixin/list-load-more";

  export default {
    name: "notice",

    mixins: [loadMore],

    data() {
      return {
        show: false,
        content: ''
      }
    },

    components: {
      List,
      Cell
    },

    created(){
      this.resetInit();
    },

    methods: {
      initData(loadMore = false) {
        let token = localStorage.getItem("token");
        this.$reqGet(NOTICES, {
          token: token,
          per_page: 20,
          page: 1
        }).then((res) => {
          return this.loadSuccess(res);
        }).catch((err) => {

        })
      },
      loadSuccess(res) {
        let list = res.data.data.list;
        if(!list){
          return this.pages;
        }
        let page = {
          "pageCount":res.data.data.pageCount,
          "currPage":res.data.data.currPage,
          "perPage":res.data.data.perPage,
          "totalCount":res.data.data.totalCount
        };
        this.items.push(...list);
        return page;
      },
      openNotice: function(notice) {
        // if(notice.type == 0){
        //   Dialog.alert({
        //     title: notice.title,
        //     message: notice.content
        //   }).then(() => {
        //     // on close
        //   });
        // }else{
        //   window.open(notice.content);
        // }
        if (notice.type == 1) {
          this.show = true;
          this.content = notice.content;
        } else if (notice.type == 2) {
          window.location.href = notice.url;
        }
      },
    }
  };
</script>

<style scoped>
  .bounced {
    width: 70%;
    height: 60%;
    padding: 20px 10px;
    font-size: 12px;
  }
</style>



